	module.exports = {"data":{"wordpressPost":{"title":"Have your research seen by the world!","content":"<p><em> </em></p>\n<p><em>BMJ Open</em> is now welcoming submissions. <a href=\"http://mc.manuscriptcentral.com/bmjopen\" target=\"_blank\">Submit now</a> – and your work could be included in the launch of our brand new open access journal.</p>\n<p>Instructions for authors are available <a href=\"http://blogs.bmj.com/bmjopen/instructions-for-authors/\" target=\"_blank\">here</a> and you can read more about <em>BMJ Open</em>&#8216;s aims and scope <a href=\"http://blogs.bmj.com/bmjopen/bmj-open-faqs/\" target=\"_blank\">here</a>.</p>\n<p>Open access has long been part of the BMJ Group ethos – <em>BMJ</em> research articles have been open access for over ten years. We are very excited about this new extension of our open access content through an entirely new journal, and we aim to be an equally invaluable resource for the medical community.</p>\n<p>If you are an author who has submitted research to any BMJ Group journal within the last six months and were declined, just <a href=\"mailto:editorial.bmjopen@bmjgroup.com\" target=\"_blank\">let us know</a> which journal you submitted to – and the manuscript number if you have it – and if the article type is appropriate we will transfer the files on your behalf. (Your article must not be under consideration by any other journal, anywhere else.)</p>\n<p>Finally, congratulations to our sister journal <em><a href=\"http://casereports.bmj.com/\" target=\"_blank\">BMJ Case Reports</a></em>, which recently won the ALPSP <a href=\"http://www.alpsp.org/ngen_public/default.asp?ID=251\" target=\"_blank\">Best New Journal 2010</a> prize. That&#8217;s something for <em>BMJ Open</em> to live up to!<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby for BMJ blog sites"}}},"pathContext":{"id":"df301e64-0f8e-5f76-9e95-5cabe7c87cf9"}}

